.top-text {
  @include font(11px, 600, 1.91, 0.01em);

  @include tablet {
    @include font(null, null, 1.36, null);
  }

  text-transform: uppercase;
  display: flex;
  align-items: center;

  &--light {
    color: rgba(157, 164, 189, 0.6);
  }

  &--dark {
    color: #303030;
  }

  &::before {
    content: '';
    width: 60px;
    height: 1px;
    background-color: currentColor;
    margin-right: 21px;
    flex-shrink: 0;
  }
}
