.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.backdrop {
  width: 100vw;
  height: 100vh;
  background-color: rgba(25, 28, 38, .39);
  transition: opacity .3s, visibility .3s;
  position: fixed;
  top: 0;
}

.modal {
  width: 500px;
  min-height: 600px;
  background-color: #fff;
  border-radius: 5px;
  padding: 25px;
  transition: transform .3s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(0);
}

.modal-close {
  width: 40px;
  height: 40px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid gray;
  border-radius: 50%;
  position: absolute;
  top: 10px;
  right: 10px;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.no-scroll {
  overflow: hidden;
}

.modal-title {
  text-align: center;
  margin-bottom: 15px;
  font-family: Roboto;
  font-size: 20px;
  font-weight: 700;
  line-height: 23px;
}

.modal-field {
