}

.modal-input {
  width: 100%;
  height: 45px;
  border: 2px solid #999;
  border-radius: 5px;
  outline: none;
  padding-left: 30px;
}

.modal-input:focus {
  border-color: #ff6c00;
}

.input-wrap {
  position: relative;
}

.modal-icon {
  position: absolute;
  top: 50%;
  left: 8px;
  transform: translateY(-50%);
}

.modal-input:focus + .modal-icon {
  fill: var(--accent-color);
}

.modal-label {
  margin-bottom: 5px;
  font-size: 18px;
  display: block;
}

.modal-textarea {
  resize: none;
  width: 100%;
  min-height: 100px;
  border: 2px solid #999;
  border-radius: 5px;
  outline: none;
}

